'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { motion } from 'framer-motion'
import { signUp } from '@/lib/api'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardFooter, CardHeader, CardTitle } from '@/components/ui/card'
import { ArrowRight, User, Mail, Key, Calendar, Phone, Building } from 'lucide-react'

export default function SignUpPage() {
  const [formData, setFormData] = useState({
    email: '',
    full_name: '',
    username: '',
    gender: '',
    date_of_birth: '',
    contact_number: '',
    institution: '',
    password: '',
  })
  const [error, setError] = useState('')
  const router = useRouter()

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      const response = await signUp(formData)
      localStorage.setItem('auth_token', response.token)
      router.push('/login')
    } catch (error) {
      setError('Signup failed. Please try again.')
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-purple-50 to-indigo-100 p-4">
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <Card className="w-full max-w-md mx-auto backdrop-blur-lg bg-white bg-opacity-50 shadow-xl border-none">
          <CardHeader>
            <CardTitle className="text-3xl font-bold text-center bg-clip-text text-transparent bg-gradient-to-r from-purple-600 to-indigo-600">
              Join Delified
            </CardTitle>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="email" className="text-sm font-medium text-gray-700">Email</Label>
                <div className="relative">
                  <Input
                    id="email"
                    name="email"
                    type="email"
                    onChange={handleChange}
                    required
                    className="pl-10 bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  />
                  <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="full_name" className="text-sm font-medium text-gray-700">Full Name</Label>
                <div className="relative">
                  <Input
                    id="full_name"
                    name="full_name"
                    onChange={handleChange}
                    required
                    className="pl-10 bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  />
                  <User className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="username" className="text-sm font-medium text-gray-700">Username</Label>
                <div className="relative">
                  <Input
                    id="username"
                    name="username"
                    onChange={handleChange}
                    required
                    className="pl-10 bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  />
                  <User className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="gender" className="text-sm font-medium text-gray-700">Gender</Label>
                <Input
                  id="gender"
                  name="gender"
                  onChange={handleChange}
                  required
                  className="bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="date_of_birth" className="text-sm font-medium text-gray-700">Date of Birth</Label>
                <div className="relative">
                  <Input
                    id="date_of_birth"
                    name="date_of_birth"
                    type="date"
                    onChange={handleChange}
                    required
                    className="pl-10 bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  />
                  <Calendar className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="contact_number" className="text-sm font-medium text-gray-700">Contact Number</Label>
                <div className="relative">
                  <Input
                    id="contact_number"
                    name="contact_number"
                    onChange={handleChange}
                    required
                    className="pl-10 bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  />
                  <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="institution" className="text-sm font-medium text-gray-700">Institution</Label>
                <div className="relative">
                  <Input
                    id="institution"
                    name="institution"
                    onChange={handleChange}
                    required
                    className="pl-10 bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  />
                  <Building className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="password" className="text-sm font-medium text-gray-700">Password</Label>
                <div className="relative">
                  <Input
                    id="password"
                    name="password"
                    type="password"
                    onChange={handleChange}
                    required
                    className="pl-10 bg-white bg-opacity-50 border-gray-300 focus:border-indigo-500 focus:ring-indigo-500"
                  />
                  <Key className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-5 w-5" />
                </div>
              </div>
              {error && <p className="text-red-500 text-sm">{error}</p>}
              <Button type="submit" className="w-full bg-gradient-to-r from-purple-600 to-indigo-600 hover:from-purple-700 hover:to-indigo-700 text-white font-semibold py-2 px-4 rounded-md transition duration-300 ease-in-out transform hover:-translate-y-1 hover:scale-105">
                Sign Up <ArrowRight className="ml-2 h-5 w-5" />
              </Button>
            </form>
          </CardContent>
          <CardFooter>
            <p className="text-sm text-gray-600 text-center w-full">
              Already have an account?{' '}
              <Link href="/login" className="text-indigo-600 hover:text-indigo-800 font-semibold transition duration-300 ease-in-out">
                Log in
              </Link>
            </p>
          </CardFooter>
        </Card>
      </motion.div>
    </div>
  )
}

